import { ToolSel } from "@/common/Toolbar";
import { ColGraph, CompatGraph, ColGraphNode, triadColorLut } from "./Graph";
import { useEffect, useReducer, useRef, useState } from "react";
import { useFlushingResizeObserver } from "@/common/resizeObserver";
import { computeGridMargins } from "@/common/Grid";
import { BucketCanvas, computeNodeBucket, drawBuckets, genBucketsJsx, randomPointInBucket, useBucketCanvas } from "./buckets";
import { foreachNaryString } from "@/common/mathUtils";
import { drawNode, GraphNodeType } from "@/common/NodeDrawing";
import { bottomColor, inputColor, midColor, redColor, topColor } from "@/common/Colors";

export default function CompatibilityGraph({
  graph: colGraph, compatGraph, onColoringChanged
} : {graph: ColGraph, compatGraph: CompatGraph, onColoringChanged: () => void})
{
  let cnv = useBucketCanvas(colGraph);

  let [, bumpCompatGraphVersion] = useReducer(x => x + 1, 0);


  function handleMouseDown(e: React.MouseEvent, nodeId: string) {
    if (!compatGraph) {
      return;
    }

    let node = compatGraph.graph.getNodeAttributes(nodeId);
    if (node && compatGraph) {
      compatGraph.activeSubgraph[node.bucketIdx] = nodeId;
      let bucket = compatGraph.buckets[node.bucketIdx];

      for (let nodeIdx = 0; nodeIdx < bucket.length; nodeIdx++) {
        colGraph.graph.getNodeAttributes(bucket[nodeIdx]).color = triadColorLut[node.coloring[nodeIdx]];
      }
      onColoringChanged();

      bumpCompatGraphVersion();
    }
  }

  function drawGraph(canvas: React.JSX.Element[], labels: React.JSX.Element[]) {
    drawBuckets(cnv, canvas, labels);

    compatGraph?.graph.forEachEdge((edgeId, attributes, source, target) => {
      let src = compatGraph.graph.getNodeAttributes(source);
      let tgt = compatGraph.graph.getNodeAttributes(target);


      let [fromX, fromY] = cnv.grid.toScreen(src.x || 0, src.y || 0);
      let [toX, toY] = cnv.grid.toScreen(tgt.x || 0, tgt.y || 0);

      let isActive = (compatGraph.activeSubgraph[src.bucketIdx] === source) && (compatGraph.activeSubgraph[tgt.bucketIdx] === target);

      let color = "gray";
      let opacity = 0.3;

      if (isActive) {
        color = topColor;
        opacity = 1.0;
      }

      let line = <line key={'edge_' + edgeId} x1={fromX} y1={fromY} x2={toX} y2={toY} stroke={color} strokeOpacity={opacity} strokeWidth={cnv.zoom * 2} />;
      canvas.push(line)
    })

    compatGraph?.graph.forEachNode((nodeId, attributes) => {
      let color = midColor;

      let active = (compatGraph.activeSubgraph[attributes.bucketIdx] === nodeId);
      if (active) {
        color = topColor;
      }


      if (!attributes.proper) {
        color = active ? redColor : inputColor;
      }

      drawNode(cnv.zoom, cnv.grid, GraphNodeType.Internal, color, attributes.x, attributes.y, canvas, labels, {
        onMouseDown: (e: React.MouseEvent) => handleMouseDown(e, nodeId),
        className: "cursor-pointer",
      });
    });

  }

  let graphCanvas: React.JSX.Element[] = [];
  let labels: React.JSX.Element[] = [];
  drawGraph(graphCanvas, labels);

  return genBucketsJsx(cnv, graphCanvas, labels);
}
